import React from "react";
import "./Card.css";


function shuffleArray(array) {
    let i = array.length - 1;
    for (; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        const temp = array[i];
        array[i] = array[j];
        array[j] = temp;
    }
    return array;
}


//everything is on the screen right?
//you want the object that is clicked to be changed from false to true BEFORE it re renders, and have that state of change remain the same.

//pass in value before you shuffle, rendering comes last.

function Card(props) { //because this is trying to access the properties of the parent class, it takes the props variable in the function.
    //the name of the onclick function within the child component, (Card) needs to match the name of the function, but like I have it below.
    //console.log(props) props represent the 3 functions, clicked, and the array. being passed down to whatever this creates.
    var shuffledChars = shuffleArray(props.characters);

    return (
        shuffledChars.map(char => (
            <button key={char.id} className="button" onClick={(e) => {
                props.handleClick(e);
                props.handleState(e);
            }}>
                <div>
                    <div className="card">
                        <div className="img-container">
                            <img alt={char.name} src={char.image} />
                        </div>
                    </div>
                </div>
            </button>
        ))

    );
}

export default Card;

